// Open simple dialogs on top of an editor. Relies on dialog.css.

export function addDialog(CodeMirror) {

  function dialogDiv(cm, template, bottom) {
    const wrap = cm.getWrapperElement();
    let dialog;
    dialog = wrap.appendChild(document.createElement('div'));
    if (bottom) { dialog.className = 'CodeMirror-dialog CodeMirror-dialog-bottom'; } else { dialog.className = 'CodeMirror-dialog CodeMirror-dialog-top'; }

    if (typeof template === 'string') {
      dialog.innerHTML = template;
    } else { // Assuming it's a detached DOM element.
      dialog.appendChild(template);
    }
    CodeMirror.addClass(wrap, 'dialog-opened');
    return dialog;
  }

  function closeNotification(cm, newVal) {
    if (cm.state.currentNotificationClose) { cm.state.currentNotificationClose(); }
    cm.state.currentNotificationClose = newVal;
  }

  CodeMirror.defineExtension('openDialog', function (template, callback, options) {
    if (!options) options = {};

    closeNotification(this, null);

    const dialog = dialogDiv(this, template, options.bottom);
    let closed = false; const me = this;
    function close(newVal) {
      if (typeof newVal === 'string') {
        inp.value = newVal;
      } else {
        if (closed) return;
        closed = true;
        CodeMirror.rmClass(dialog.parentNode, 'dialog-opened');
        dialog.parentNode.removeChild(dialog);
        me.focus();

        if (options.onClose) options.onClose(dialog);
      }
    }

    var inp = dialog.getElementsByTagName('input')[0]; let button;
    if (inp) {
      inp.focus();

      if (options.value) {
        inp.value = options.value;
        if (options.selectValueOnOpen !== false) {
          inp.select();
        }
      }

      if (options.onInput) { CodeMirror.on(inp, 'input', (e) => { options.onInput(e, inp.value, close); }); }
      if (options.onKeyUp) { CodeMirror.on(inp, 'keyup', (e) => { options.onKeyUp(e, inp.value, close); }); }

      CodeMirror.on(inp, 'keydown', (e) => {
        if (options && options.onKeyDown && options.onKeyDown(e, inp.value, close)) { return; }
        if (e.keyCode == 27 || (options.closeOnEnter !== false && e.keyCode == 13)) {
          inp.blur();
          CodeMirror.e_stop(e);
          close();
        }
        if (e.keyCode == 13) callback(inp.value, e);
      });

      if (options.closeOnBlur !== false) CodeMirror.on(inp, 'blur', close);
    } else if (button = dialog.getElementsByTagName('button')[0]) {
      CodeMirror.on(button, 'click', () => {
        close();
        me.focus();
      });

      if (options.closeOnBlur !== false) CodeMirror.on(button, 'blur', close);

      button.focus();
    }
    return close;
  });

  CodeMirror.defineExtension('openConfirm', function (template, callbacks, options) {
    closeNotification(this, null);
    const dialog = dialogDiv(this, template, options && options.bottom);
    const buttons = dialog.getElementsByTagName('button');
    let closed = false; const me = this; let blurring = 1;
    function close() {
      if (closed) return;
      closed = true;
      CodeMirror.rmClass(dialog.parentNode, 'dialog-opened');
      dialog.parentNode.removeChild(dialog);
      me.focus();
    }
    buttons[0].focus();
    for (let i = 0; i < buttons.length; ++i) {
      var b = buttons[i];
      (function (callback) {
        CodeMirror.on(b, 'click', (e) => {
          CodeMirror.e_preventDefault(e);
          close();
          if (callback) callback(me);
        });
      }(callbacks[i]));
      CodeMirror.on(b, 'blur', () => {
        --blurring;
        setTimeout(() => { if (blurring <= 0) close(); }, 200);
      });
      CodeMirror.on(b, 'focus', () => { ++blurring; });
    }
  });

  /*
   * openNotification
   * Opens a notification, that can be closed with an optional timer
   * (default 5000ms timer) and always closes on click.
   *
   * If a notification is opened while another is opened, it will close the
   * currently opened one and open the new one immediately.
   */
  CodeMirror.defineExtension('openNotification', function (template, options) {
    closeNotification(this, close);
    const dialog = dialogDiv(this, template, options && options.bottom);
    let closed = false; let doneTimer;
    const duration = options && typeof options.duration !== 'undefined' ? options.duration : 5000;

    function close() {
      if (closed) return;
      closed = true;
      clearTimeout(doneTimer);
      CodeMirror.rmClass(dialog.parentNode, 'dialog-opened');
      dialog.parentNode.removeChild(dialog);
    }

    CodeMirror.on(dialog, 'click', (e) => {
      CodeMirror.e_preventDefault(e);
      close();
    });

    if (duration) { doneTimer = setTimeout(close, duration); }

    return close;
  });
}
